<template>
  <v-row justify="end" class="mr-1">
    <v-dialog v-model="dialog" persistent max-width="290">
      <template v-slot:activator="{ on, attrs }">
        <v-btn color="primary" dark v-bind="attrs" v-on="on">
          <v-icon left>mdi-timetable</v-icon>Edit Time
        </v-btn>
      </template>
      <v-card>
        <v-container>
          <v-layout row wrap>
            <v-flex xs12>
              <v-card-title>Edit Meetup Time</v-card-title>
            </v-flex>
          </v-layout>
          <v-divider></v-divider>
          <v-layout row wrap>
            <v-flex xs12>
              <v-time-picker ampm-in-title v-model="editableTime" max-width="100%" actions>
                <template>
                  <div class="editTime">
                    <div class="btn_close">
                      <v-btn class="primary--text darken-1" flat @click="dialog = false">
                        <v-icon left>mdi-close</v-icon>Close
                      </v-btn>
                    </div>
                    <div class="btn_save">
                      <v-btn class="primary--text darken-1" flat @click="onSaveChanges">
                        <v-icon left>mdi-check</v-icon>Save
                      </v-btn>
                    </div>
                  </div>
                </template>
              </v-time-picker>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
export default {
  props: ["meetup"],
  data() {
    return {
      dialog: false,
      editableTime: null,
    };
  },
  methods: {
    onSaveChanges() {
      const newDate = new Date(this.meetup.date);
      const hours = this.editableTime.match(/^(\d+)/)[1];
      const minutes = this.editableTime.match(/:(\d+)/)[1];
      newDate.setHours(hours);
      newDate.setMinutes(minutes);
      this.$store.dispatch("updateMeetupData", {
        id: this.meetup.id,
        date: newDate,
      });
    },
    created() {
      this.editableTime = new Date(this.meetup.date).toString();
    },
  },
};
</script>

<style scoped>
.editTime {
  display: flex;
  flex-direction: column;
}
.btn_close {
  display: block;
  width: 200%;
  border: none;
  background-color: #e70a0ad2;
  border-radius: 5px;
  padding: 5px 16px;
  margin: 0.25rem auto;
  cursor: pointer;
  text-align: center;
}
.btn_save {
  display: block;
  width: 200%;
  border: none;
  background-color: #4caf50;
  border-radius: 5px;
  padding: 5px 16px;
  margin: 0.25rem auto;
  cursor: pointer;
  text-align: center;
}
</style>