<template>
  <v-container class="carousel">
    <v-layout row>
      <v-flex xs12 sm6 offset-sm3>
        <h4 class="primary--text">Create a new Meetup</h4>
      </v-flex>
    </v-layout>
    <v-layout row>
      <v-flex xs12>
        <form @submit.prevent="onCreateMeetup(onSubmit)">
          <!-- title -->
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-text-field name="title" label="Title" id="title" v-model="title" required></v-text-field>
            </v-flex>
          </v-layout>
          <!-- location -->
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-text-field
                name="location"
                label="Location"
                id="location"
                v-model="location"
                required
              ></v-text-field>
            </v-flex>
          </v-layout>
          <!-- image -->
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-btn
                raised
                class="primary mb-4"
                
                @click="onPickFile"
              >
              <v-icon left>
                mdi-camera
              </v-icon>
              Upload image</v-btn>
              <input
                type="file"
                style="display: none;"
                ref="fileInput"
                accept="image/*"
                @change="onFilePicked"
              />
            </v-flex>
          </v-layout>
          <!-- preview of the image -->
          <v-layout>
            <v-flex sx12 sm6 offset-sm3 v-if="imageUrl">
              <img :src="imageUrl" width="100%" height="80%" />
            </v-flex>
          </v-layout>
          <!-- description -->
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-textarea
                filled
                prepend-inner-icon="mdi-comment"
                name="description"
                label="Description"
                id="description"
                counter="500"
                v-model="description"
                required
              ></v-textarea>
            </v-flex>
          </v-layout>
          <!-- Calendar -->
          <v-layout row class="pb-1">
            <v-flex xs12 sm6 offset-sm3>
              <h4>Chose a Date & Time</h4>
            </v-flex>
          </v-layout>
          <!-- Date picker -->

          <v-layout row>
            <v-flex xs12 sm6 offset-sm3 class="mb-2">
              <v-date-picker v-model="date"></v-date-picker>
              <!-- <p>{{ date }}</p> -->
            </v-flex>
          </v-layout>

          <!-- Time picker -->

          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-time-picker ampm-in-title v-model="time"></v-time-picker>
              <!-- <p>{{ time }}</p> -->
            </v-flex>
          </v-layout>

          <!-- Submit Button -->
          <v-layout row>
            <v-flex sx12 sm6 offset-sm3>
              <v-btn class="primary" :disabled="!formIsValid" type="submit">Create Meetup</v-btn>
              <!-- gia debugging tis time -->
              <!-- {{ submittableDateTime }} -->
            </v-flex>
          </v-layout>
        </form>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      title: "",
      location: "",
      imageUrl: "",
      description: "",
      date: new Date().toISOString().substr(0, 10),
      time: new Date(),
      image: null,
    };
  },
  computed: {
    formIsValid() {
      return (
        this.title !== "" &&
        this.location !== "" &&
        this.imageUrl !== "" &&
        this.description !== ""
      );
    },
    submittableDateTime() {
      const date = new Date(this.date);
      if (typeof this.time === "string") {
        const hours = this.time.match(/^(\d+)/)[1];
        const minutes = this.time.match(/:(\d+)/)[1];
        date.setHours(hours);
        date.setMinutes(minutes);
      } else {
        date.setHours(this.time.getHours());
        date.setMinutes(this.time.getMinutes());
      }

      console.log(date);
      return date;
    },
  },
  methods: {
    onCreateMeetup() {
      if (!this.formIsValid) {
        return;
      }
      if (!this.image){
        return
      }
      const meetupData = {
        title: this.title,
        location: this.location,
        image: this.image,
        description: this.description,
        date: new Date(),
      };

      this.$store.dispatch("createMeetup", meetupData);
      this.$router.push("/meetups");
    },
    onPickFile() {
      this.$refs.fileInput.click();
    },
    onFilePicked(event) {
      const files = event.target.files;
      let filename = files[0].name;
      if (filename.lastIndexOf(".") <= 0) {
        return alert("Please add a valid file!");
      }
      const fileReader = new FileReader();
      fileReader.addEventListener("load", () => {
        this.imageUrl = fileReader.result;
      });
      fileReader.readAsDataURL(files[0]);
      this.image = files[0];
    },
  },
};
</script>

<style scoped>
@media only screen and (max-width: 599px) {
  .carousel {
    max-width: 70%;
    /* padding: 0 5%; */
    margin: 0 auto;
  }
}
</style>
