<template>
  <v-row justify="end" class="mr-1">
    <v-dialog v-model="dialog" persistent max-width="350px">
      <template v-slot:activator="{ on, attrs }">
        <v-btn color="primary" dark v-bind="attrs" v-on="on">
          <v-icon left>mdi-calendar-today</v-icon>Edit Date
        </v-btn>
      </template>
      <v-card>
        <v-container>
          <v-layout row wrap>
            <v-flex xs12>
              <v-card-title>Edit Meetup Date</v-card-title>
            </v-flex>
          </v-layout>
          <v-divider></v-divider>
          <v-layout row wrap>
            <v-flex xs12>
              <v-date-picker v-model="editableDate" width="100%" actions>
                <template>
                  <div class="editDate">
                    <div>
                      <v-btn class="primary--text darken-1 btn_close" flat @click="dialog = false">
                        <v-icon left>mdi-close</v-icon>Close
                      </v-btn>
                    </div>
                    <div>
                      <v-btn class="primary--text darken-1 btn_open" flat @click="onSaveChanges">
                        <v-icon left>mdi-check</v-icon>Save
                      </v-btn>
                    </div>
                  </div>
                </template>
              </v-date-picker>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
export default {
  props: ["meetup"],
  data() {
    return {
      dialog: false,
      editableDate: null,
    };
  },
  methods: {
    onSaveChanges() {
      const newDate = new Date(this.meetup.date);
      const newDay = new Date(this.editableDate).getUTCDate();
      const newMonth = new Date(this.editableDate).getUTCMonth();
      const newYear = new Date(this.editableDate).getUTCFullYear();
      newDate.setUTCDate(newDay);
      newDate.setUTCMonth(newMonth);
      newDate.setUTCFullYear(newYear);
      this.$store.dispatch("updateMeetupData", {
        id: this.meetup.id,
        date: newDate,
      });
    },
    created() {
      let date = new Date(this.meetup.date); //set Date to the date of meetup

      let day = date.setUTCDate();
      let month = date.getUTCMonth();
      let year = date.getUTCFullYear();

      this.editDate = year + "-" + month + "-" + day;
    },
  },
};
</script>

<style scoped>
.editDate {
  display: flex;
  flex-direction: column;
}

.btn_close {
  width: 320%;
  margin: 0.25rem auto;
  text-align: center;
}

.btn_open {
  width: 320%;
  margin: 0.25rem auto;
  text-align: center;
}
</style>