<template>
  <v-container>
    <v-layout row wrap v-if="loading">
      <v-flex xs12 class="text-center">
        <v-progress-circular indeterminate color="primary" :width="7" :size="70" v-if="loading"></v-progress-circular>
      </v-flex>
    </v-layout>
    <v-layout row wrap v-else>
      <v-flex xs12 class="carousel">
        <v-card class="bg-gray">
          <v-card-title>
            <h5 class="primary--text">{{ meetup.title }}</h5>
            <!-- Edit the meetup -->
            <template v-if="userIsCreator">
              <v-spacer></v-spacer>
              <app-edit-meetup-details-dialog :meetup="meetup"></app-edit-meetup-details-dialog>
            </template>
          </v-card-title>
          <v-img :src="meetup.imageUrl" max-width="100%" max-height="50vh"></v-img>
          <v-text>
            <div>
              <v-card-subtitle>
                <p>
                  <v-icon right>mdi-calendar-range</v-icon>
                  {{ meetup.date | date}}
                </p>
                <p>
                  <v-icon right>mdi-map-marker</v-icon>
                  {{ meetup.location }}
                </p>

                <div class="editDateTime">
                  <div>
                    <!-- edit date -->
                    <app-edit-meetup-date-dialog :meetup="meetup" v-if="userIsCreator"></app-edit-meetup-date-dialog>
                  </div>
                  <!-- edit time -->
                  <div>
                    <app-edit-meetup-time-dialog :meetup="meetup" v-if="userIsCreator"></app-edit-meetup-time-dialog>
                  </div>
                </div>
              </v-card-subtitle>
            </div>
            <div class="mx-6 pb-2">{{ meetup.description }}</div>
          </v-text>
          <v-card-action>
            <div class="btn-right">
              <!-- gia na paei sthn antitheti meria -->
              <div>
                <!-- push the register -->
              </div>
              <!-- <v-btn class="mx-10 my-3 green white--text">
                <v-icon left>mdi-pencil</v-icon>Register
              </v-btn>-->
              <!-- Register or Unregister Button -->
              <app-meetup-register-dialog
                :meetupId="meetup.id"
                v-if="userIsAuthenticated && !userIsCreator"
              ></app-meetup-register-dialog>
            </div>
          </v-card-action>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<style scoped>
.editDateTime {
  display: flex;
  justify-content: space-evenly;
  margin: 1rem auto;
}

.btn-right {
  display: flex;
  justify-content: space-between;
}

.carousel {
  max-width: 100%;
  padding: 0 5%;
  margin: 0 auto;
}

@media only screen and (min-width: 1900px) {
  .carousel {
    max-width: 80%;
  }
}

.bg-gray {
  background-color: #f3f3f3;
}
</style>

<script>
export default {
  props: ["id"],
  computed: {
    meetup() {
      return this.$store.getters.loadedMeetup(this.id);
    },
    userIsAuthenticated() {
      return (
        this.$store.getters.user !== null &&
        this.$store.getters.user !== undefined
      );
    },
    userIsCreator() {
      if (!this.userIsAuthenticated) {
        return false;
      }
      return this.$store.getters.user.id === this.meetup.creatorId;
    },
    loading() {
      return this.$store.getters.loading;
    },
  },
};
</script>
