<template>
  <v-container>
    <v-layout row v-if="error">
      <v-flex xs12 sm6 offset-sm3>
        <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
      </v-flex>
    </v-layout>
    <v-layout row class="text-center my-2" large>
      <v-flex xs12 sm6 offset-sm3>
        <h2 class="primary--text">Sign in</h2>
      </v-flex>
    </v-layout>
    <v-layout row class="my-4 carousel">
      <v-flex xs12 sm6 offset-sm3>
        <v-card>
          <v-card-text>
            <v-container>
              <form @submit.prevent="onSignin">
                <!-- email -->
                <v-layout row class="mb-2">
                  <v-flex xs12>
                    <v-text-field
                      name="email"
                      label="E-Mail"
                      :rules="emailRules"
                      id="email"
                      v-model="email"
                      type="mail"
                      required
                    ></v-text-field>
                  </v-flex>
                </v-layout>
                <!-- password -->
                <v-layout row class="mb-2">
                  <v-flex xs12>
                    <v-text-field
                      name="password"
                      label="Password"
                      id="password"
                      v-model="password"
                      :append-icon="show3 ? 'mdi-eye' : 'mdi-eye-off'"
                      :rules="[rules.required, rules.min]"
                      :type="show3 ? 'text' : 'password'"
                      hint="At least 6 characters"
                      class="input-group--focused"
                      @click:append="show3 = !show3"
                    ></v-text-field>
                  </v-flex>
                </v-layout>

                <!-- submit button -->
                <v-layout row>
                  <v-flex xs12>
                    <v-btn
                      class="rounded-pill primary"
                      style="width:100%"
                      type="submit"
                      :disabled="loading"
                      :loading="loading"
                      >Sign up</v-btn
                    >
                  </v-flex>
                </v-layout>
              </form>
            </v-container>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      show3: false,
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+\..+/.test(v) || 'E-mail must be valid',
      ],
      password: '',
      confirmPassword: '',
      rules: {
        required: value => !!value || 'Required.',
        min: v => v.length >= 8 || 'Password is required',
      },
    };
  },
  computed: {
    user() {
      return this.$store.getters.user;
    },
    error() {
      return this.$store.getters.error;
    },
    loading() {
      return this.$store.getters.loading;
    },
  },
  watch: {
    user(value) {
      if (value !== null && value !== undefined) {
        this.$router.push('/');
      }
    },
  },
  methods: {
    onSignin() {
      // Vuex
      this.$store.dispatch('signUserIn', {
        email: this.email,
        password: this.password,
      });
    },
    onDismissed() {
      this.$store.dispatch('clearError');
    },
  },
};
</script>

<style scoped>
.carousel {
  max-width: 100%;
  margin: 0 auto;
}

@media only screen and (max-width: 600px) {
  .carousel {
    max-width: 80%;
    padding: 0 5%;
  }
}
</style>
