import Vue from 'vue';
import App from './App.vue';
import * as firebase from 'firebase';
import vuetify from './plugins/vuetify';
import router from './router';
import {store} from './store';
import DateFilter from './filters/date';
import AlertCmp from './components/Shared/Alert';
import EditMeetupDetailsDialog from './components/Meetup/Edit/EditMeetupDetailsDialog.vue';
import EditMeetupDateDialog from './components/Meetup/Edit/EditMeetupDateDialog.vue';
import EditMeetupTimeDialog from './components/Meetup/Edit/EditMeetupTimeDialog.vue';
import RegisterDialog from './components/Meetup/Registration/RegisterDialog.vue';

Vue.config.productionTip = false;

Vue.filter('date', DateFilter);
Vue.component('app-alert', AlertCmp);
Vue.component('app-edit-meetup-details-dialog', EditMeetupDetailsDialog);
Vue.component('app-edit-meetup-date-dialog', EditMeetupDateDialog);
Vue.component('app-edit-meetup-time-dialog', EditMeetupTimeDialog);
Vue.component('app-meetup-register-dialog', RegisterDialog);

new Vue({
  vuetify,
  router,
  store,
  render: h => h(App),
  created() {
    firebase.initializeApp({
      // start here
      apiKey: 'AIzaSyDKR_WCxeKytfszwXxsJfoc-K_vXXAy7CQ',
      authDomain: 'meetup-vue-project-b9c2f.firebaseapp.com',
      databaseURL: 'https://meetup-vue-project-b9c2f.firebaseio.com',
      projectId: 'meetup-vue-project-b9c2f',
      storageBucket: 'gs://meetup-vue-project-b9c2f.appspot.com',
      messagingSenderId: '685763649458',
      appId: '1:685763649458:web:4ed735afc8352125e60eab',
      measurementId: 'G-ZB5KXN2FPP',

      // apiKey: process.env.VUE_APP_apiKey,
      // authDomain: process.env.VUE_APP_authDomain,
      // databaseURL: process.env.VUE_APP_databaseURL,
      // projectId: process.env.VUE_APP_projectId,
      // storageBucket: process.env.VUE_APP_storageBucket,
      // messagingSenderId: process.env.VUE_APP_messagingSenderId,
      // appId: process.env.VUE_APP_appId,
      // measurementId: process.env.VUE_APP_measurementId,
    });
    firebase.auth().onAuthStateChanged(user => {
      if (user) {
        this.$store.dispatch('autoSignIn', user);
        this.$store.dispatch('fetchUserData');
      }
    });
    this.$store.dispatch('loadMeetups');
  },
}).$mount('#app');
