<template>
  <v-row justify="end" class="mr-1">
    <v-dialog v-model="registerDialog" persistent max-width="450">
      <template v-slot:activator="{ on, attrs }">
        <v-btn color="primary mb-8 mr-6" dark v-bind="attrs" v-on="on">
          <v-icon left class="error" v-if="userIsRegistered">mdi-close-octagon-outline</v-icon>
          <v-icon left class="green" v-else>mdi-checkbox-marked-circle-outline</v-icon>
          {{ userIsRegistered ? `Unregister` : 'Register'}}
        </v-btn>
      </template>
      <v-card>
        <v-container>
          <v-layout row wrap>
            <v-flex xs12>
              <v-card-title v-if="userIsRegistered">Unregister from Meetup?</v-card-title>
              <v-card-title v-else>Register from Meetup?</v-card-title>
            </v-flex>
          </v-layout>
          <v-divider></v-divider>
          <v-layout row wrap>
            <v-flex xs12>
              <v-card-text>You can always change your decision later on for free...</v-card-text>
            </v-flex>
          </v-layout>
          <!-- actions -->
          <v-layout row wrap>
            <v-flex xs12>
              <v-card-actions> 
                  <v-spacer></v-spacer>
                <v-btn class="red--text darken-1" flat @click="registerDialog = false">Cancel</v-btn>
                <v-btn class="green--text darken-1" flat @click="onAgree">Confirm</v-btn>
              </v-card-actions>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card>
    </v-dialog>
  </v-row>
</template>

<script>
export default {
  props: ["meetupId"],
  data() {
    return {
      registerDialog: false,
    };
  },
  computed: {
    userIsRegistered() {
      return (
        this.$store.getters.user.registeredMeetups.findIndex((meetupId) => {
          return meetupId === this.meetupId;
        }) >= 0
      );
    },
  },
  methods: {
    onAgree() {
      if (this.userIsRegistered) {
        this.$store.dispatch("unregisterUserFromMeetup", this.meetupId);
      } else {
        this.$store.dispatch("registerUserForMeetup", this.meetupId);
      }
    },
  },
};
</script>

<style scoped>
.editDate {
  display: flex;
  flex-direction: column;
}

.btn_close {
  width: 320%;
  margin: 0.25rem auto;
  text-align: center;
}

.btn_open {
  width: 320%;
  margin: 0.25rem auto;
  text-align: center;
}
</style>