<template>
  <v-alert class="error" dismissible @input="onClose" :value="true">{{text}}</v-alert>
</template>
<!-- Ama den leitourgei to alert na afairesw to ":value="true"" -->

<script>
export default {
  props: ["text"],
  methods: {
    onClose() {
      this.$emit("dismissed");
    },
  },
};
</script>