import * as firebase from 'firebase';

export default ({
  state: {
    loadedMeetups: [
      {
        imageUrl: 'https://www.we-love-crete.com/images/sitia-crete-dusk.jpg',
        id: 'afafa1234',
        title: 'Meetup in Crete',
        date: '2020-07-17',
        location: 'Sitia Crete',
        description: 'Tha einai kai gamw',
      },
      {
        imageUrl:
          'https://mastermehmed.com/wp-content/uploads/2020/02/paris-shutterstock-1400x500.jpg',
        id: 'afafa5678',
        title: 'Meetup in Paris',
        date: '2020-07-19',
        location: 'Paris',
        description: "It's Paris!",
      },
    ],
  },
  mutations: {
    setLoadedMeetups(state, payload) {
      state.loadedMeetups = payload;
    },
    createMeetup(state, payload) {
      state.loadedMeetups.push(payload);
    },
    updateMeetup(state, payload) {
      const meetup = state.loadedMeetups.find(meetup => {
        return meetup.id === payload.id;
      });
      if (payload.title) {
        meetup.title = payload.title;
      }
      if (payload.description) {
        meetup.description = payload.description;
      }
      if (payload.date) {
        meetup.date = payload.date;
      }
    },
  },
  actions: {
    
    loadMeetups({commit}) {
      commit('setLoading', true);
      firebase
        .database()
        .ref('meetups')
        .once('value')
        .then(data => {
          const meetups = [];
          const obj = data.val();
          for (let key in obj) {
            meetups.push({
              id: key,
              title: obj[key].title,
              description: obj[key].description,
              imageUrl: obj[key].imageUrl,
              date: obj[key].date,
              location: obj[key].location,
              creatorId: obj[key].creatorId,
            });
          }

          commit('setLoadedMeetups', meetups);
          commit('setLoading', false);
        })
        .catch(error => {
          console.log(error);
        });
    },

    createMeetup({commit, getters}, payload) {
      // image is passed as file, need to upload it and then store the imageUrl
      const dbRef = firebase
        .database()
        .ref('meetups')
        .push();
      const key = dbRef.key;
      const filename = payload.image.name;
      const ext = filename.slice(filename.lastIndexOf('.'));
      const storageRef = firebase
        .storage()
        .ref('meetups')
        .child(key + '.' + ext);

      storageRef
        .put(payload.image)
        .then(() => {
          // file uploaded
          return storageRef.getDownloadURL();
        })
        .then(downloadUrl => {
          const meetup = {
            title: payload.title,
            location: payload.location,
            description: payload.description,
            date: payload.date.toISOString(),
            id: key,
            creatorId: getters.user.id,
            imageUrl: downloadUrl,
          };
          commit('createMeetup', meetup);
          return dbRef.set(meetup);
        })
        .then(() => {
          console.log('meeting created');
        })
        .catch(error => {
          console.error('error: ' + error);
        });
    },

    updateMeetupData({commit}, payload) {
      commit('setLoading', true);
      const updateObj = {};
      if (payload.title) {
        updateObj.title = payload.title;
      }
      if (payload.description) {
        updateObj.description = payload.description;
      }
      if (payload.date) {
        updateObj.date = payload.date;
      }
      firebase
        .database()
        .ref('meetups')
        .child(payload.id)
        .update(updateObj)
        .then(() => {
          commit('setLoading', false);
          commit('updateMeetup', payload);
        })
        .catch(error => {
          console.log(error);
          commit('setLoading', false);
        });
    },
  },

  getters: {
    loadedMeetups(state) {
      return state.loadedMeetups.sort((meetupA, meetupB) => {
        return meetupA.date > meetupB.date;
      });
    },
    featureMeetups(state, getters) {
      return getters.loadedMeetups.slice(0, 5);
    },
    loadedMeetup(state) {
      return meetupId => {
        return state.loadedMeetups.find(meetup => {
          return meetup.id == meetupId;
        });
      };
    },
  },
});
